body, html {
    margin: 0;
    padding: 00;
    overflow: hidden;
    width: 100%;
    height: 100%;
  }
  
  #canvas-container {
    width: 100%;
    height: 100%;
    position:relative;
    grid-column: 3/4;
  }

  .container {
    display: grid;
    grid-template-columns: 50px 200px 1fr 200px;
    grid-template-rows:50px 50px 50px 50px 50px 50px 50px 1fr;
    height:100vh;
    column-gap: 0px;
  }

  .icon{
    padding: 0.5em;
    background-color: #fb7185;
    font-weight: 700;
    color: blueviolet;
    border-top: 2px solid black;
    border-bottom: 2px solid black;
    border-right:0px solid black;
    border-radius: 0px;
    position: relative
    }



  
  #icon-1 {
    border-top:2px solid black;
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 2;
    grid-row-end:3;
    background-color: blue;
  }



  #icon-2 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 3;
    grid-row-end:4;
    background-color: green;
  }

  #icon-3 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 4;
    grid-row-end:5;
    background-color:red;
  }

  #icon-4 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 5;
    grid-row-end:6;
    background-color: violet;
  
  }

  #icon-5 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 6;
    grid-row-end:7;
    background-color: greenyellow;
  }

  #icon-6 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 7;
    grid-row-end:8;
    border-bottom:2px solid black;
    background-color: aquamarine;
  
  }

  .sidebar{
    padding: 0.5em;
    font-weight: 700;
    color: white;
    border: 2px solid black;
    border-radius: 2px;
    grid-column-start: 2;
    grid-column-end:3;
    grid-row-start: 2;
    grid-row-end:9;  
    position:relative;
  }


  #sidebar1 {
    background-color: blue;
    z-index: 20;
  }

  #sidebar2 {
    background-color: green;
  }

  #sidebar3 {
    background-color: red;
  }

  #sidebar4 {
    background-color: violet;
  }

  #sidebar5{
    background-color: greenyellow;
  }

  #sidebar6{
    background-color: aquamarine;
  }

.input1{
  width: 20px;;
}

#sidebarR{
  background-color: aquamarine;
  grid-column-start:4;
  grid-column-end:5;
}



